<template>
  <div class="coupon-item">
    <div class="content-wrapper">
      <div class="value-wrapper">
        <coupon-value-label :couponValue='couponData.couponValue' :textColor="'#ffffff'" :couponType='couponData.couponType'></coupon-value-label>
        <!-- <span class="val-icon"></span>
        <span class="val-num" :class="{bigVal:isBigVal}">{{couponData.couponValue}}</span> -->
      </div>
      <div class="info-wrapper">
        <div class="info">
          <p class="info-text">
            <span v-if="couponData.scope==='PRODUCT'" class="singleItem"></span>
            <span v-if="couponData.scope==='ORDER'" class="singleItem"></span>
            {{couponData.description}}
          </p>
          <p class="info-text">{{couponData.couponTemplateName}}</p>
          <p class="info-text" v-if="couponData.startTime">{{couponData.startTime | formatDateEndOfDay}}  {{couponData.endTime | formatDateEndOfDay}}</p>
          <p class="info-text" v-else>{{couponData.validityPeriod}}</p>
        </div>
        <div class="receive">
          <div v-if="couponData.displayType==1" class="not-received">
            <span class="receive-btn" @click="receive"></span>
          </div>
          <div v-if="couponData.displayType==2" class="received">
            <!-- <span class="receive-disabled"></span> -->
          </div>
          <div v-if="couponData.displayType==5" class="sold-out">
            <img :src="`${$cdnImageUrl}/shop/coupon-sold-out.png`" class="sold-out-img">
          </div>
        </div>
      </div>
    </div>

    <img class="type-icon" :src="iconSrc" alt="">
    <img class="new-coupon-bg" :src="`${$cdnImageUrl}/shop/order-voucher-bg.png`" />
  </div>
</template>

<script>
import couponValueLabel from '@/components/couponValueLabel'

export default {
  name: 'couponListAvaliItem',
  components: { couponValueLabel },
  props: {
    couponData: Object
  },
  data () {
    return {}
  },
  created () {},
  mounted () {},
  beforeDestroy () {},
  methods: {
    receive () {
      const time = {
        startTime: this.couponData.startTime ? this.couponData.startTime.split(' ')[0] : '',
        endTime: this.couponData.endTime ? this.couponData.endTime.split(' ')[0] : ''
      }
      this.$emit('receive', time)
    }
  },
  computed: {

    isBigVal () {
      return this.couponData.couponValue > 1000
    },
    couponIcon () {
      const _couponIcon = {
        ORDER: `${this.$cdnImageUrl}/shop/kaquan.png`,
        SCENE: `${this.$cdnImageUrl}/mine/mine-voucher-scene.png`,
        // ORDER: `${this.$cdnImageUrl}/shop/icon-coupon-plat.png`,
        SHOP: `${this.$cdnImageUrl}/shop/dianpuquan.png`
      }
      return _couponIcon
    },
    iconSrc () {
      let type = this.couponData.couponUseType

      if (this.couponData.applyType) type = 'SCENE'

      return this.couponIcon[type] || type
    }
  },
  watch: {}
}
</script>

