<template>
  <div class="picker-container">
    <div class="content">
        <div class="track-info">
            <div class="header">
                <span class="title"></span>
                <span class="close-btn" @click="close"></span>
            </div>
            <div class="track-btn-container" v-if="isWaOcO">
             <div class="track-btn"></div>
            </div>
        </div>
        <div class="time-info">
            <div class="header">
                
            </div>
            <div class="time-content">
                <div class="time-item" :class="{'active': !pickerChangeStatus ? dateSelect ? item === dateSelect : index === 0 : item.split('(')[0] === dateSelect.split('(')[0]}" v-for="(item, index) in pickerData" :key="item" @click="changeDate(item)">{{item}}</div>
                <div class="time-bottom"></div>
            </div>
        </div>

    </div>
    <div class="btn-wrap">
      <div class="btn"  @click="fnConfirm"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['pickerData', 'currentDate', 'isWaOcO'],
  data() {
    return {
      pickerChangeStatus: false,
      dateSelect: ''
    }
  },
  watch: {
    currentDate: {
      handler(n, o) {
        this.dateSelect = n
      },
    },
  },
  mounted() {
  },
  methods: {
    /**
       * @description 
      */
    close() {
      //  
      if (this.pickerChangeStatus) {
        this.dateSelect = this.currentDate
      } else {
        this.dateSelect = this.pickerData[0]
      }
      this.$emit('closePicker')
    },
    fnConfirm() {
      this.$emit('confirm', this.dateSelect)
      this.pickerChangeStatus = true
    },
    changeDate(v) {
      this.dateSelect = v
    },
    showCurrentDate() {
      this.$el.querySelector('.active').scrollIntoView({
        block: 'start',
      }) // 
    },
  },
}
</script>
