<template>
  <!--  -->
  <div class="voucher-list__wrapper">
    <div class="li voucher-list-item" v-for="(item, index) in voucherList" :key="index">
      <coupon-item :couponData="item" @receive="fnReceiveVoucher(item, index, $event)"></coupon-item>
    </div>
  </div>
</template>

<script>
import couponListAvaliItem from './coupon-list-unselect-item'
import { receiveCoupon } from '@/http/service/goods'
export default {
  props: {
    streetId: String,
    voucherList: {
      type: Array,
    },
  },
  components: {
    couponItem: couponListAvaliItem,
  },
  methods: {
    fnReceiveVoucher(item, index, e) {
      console.log(item, '-- item --')
      if (item.displayType === 2) {
        // 
        return
      }
      this.receiveCoupon(item, index, e)
    },
    receiveCoupon(item, index, e) {
      // console.log(e, 'event ****')

      receiveCoupon({
        activityCode: item.activityCode,
        couponTemplateCode: item.couponTemplateCode,
        sourceChannel: item.sourceChannel,
        streetId: this.streetId,
      }).then(res => {
        if (res.success) {
          //
          this.$emit('received', item, index, '2')
          // this.voucherList[data.index].displayType = '2'
          // this.voucherList = [...this.voucherList]
          this.receiveToast(`${e.startTime}-${e.endTime}`)
          // this.$toast(res.message)
        } else {
          this.$emit('received', item, index, '5')
          this.$toast(res.message)
          // this.receiveToast()
        }
      })
    },
    receiveToast(time) {
      // console.log(time)
      const icon = time ? require('@/assets/imgs/cart/icon-receive-success.png') : require('@/assets/imgs/cart/icon-receive-error.png')
      const message = time ? '' : ''
      this.$toast({ message, icon, duration: 1500 })
      if (time) {
        this.$nextTick(() => {
          const wrapper = document.querySelector('.van-toast')
          if (wrapper.querySelectorAll('.toast-add-text').length) {
            return
          }
          const text = document.createElement('span')
          text.className = 'toast-add-text'
          text.innerHTML = `${time}`
          wrapper.appendChild(text)
          const removeTimer = window.setTimeout(() => {
            wrapper.removeChild(text)
            window.clearTimeout(removeTimer)
          }, 1550)
        })
      }
    },
  },
}
</script>

<style scoped>
.voucher-list__wrapper {
  position: relative;
  height: 5.88rem;
  margin-bottom: 0.24rem;
  overflow-y: auto;
  padding: 0 0.32rem;
}
.voucher-list-item {
  position: relative;
  z-index: 1;
  height: 2.06rem;
  margin-bottom: 0.24rem;
}
</style>
