<template>
  <div class="right-content">
    <p class="title"></p>
    <div class="main">
      <div
        v-for="(item, index) in getOrderRightInfos"
        :key="index"
        class="main-box"
        @click.stop="handleChecked(item)"
      >
        <div class="main-l">
          <div class="main-item">
            <span class="main-title"></span>
            <span class="main-info">{{item.rightsName}}</span>
          </div>
          <div class="main-item">
            <span class="main-title"></span>
            <span class="main-info">{{item.giftType | giftTypeFilter}}</span>
          </div>
          <div class="main-item">
            <span class="main-title"></span>
            <span class="main-info">{{item.activityStartDate}}  {{item.activityEndDate}}</span>
          </div>
          <div class="main-item">
            <span class="main-title"></span>
            <span class="main-info">{{item.activationType | activedFilter}}</span>
          </div>
          <div class="main-item right-desc">
            <span class="main-title"></span>
            <span
              class="main-info right-desc-info"
              @click.stop="handleShowDesc(item)"
            >
              <div
                class="content"
                :class="item.showDesc ? '' : 'wrap'"
                v-html="filterRightsDesc(item.rightsDesc)"
              ></div>
              <img
                class="img"
                :class="item.showDesc ? 'yes' : 'no'"
                :src="`${$cdnImageUrl}/cart/icon-arrow-up1.png`"
              />
            </span>
          </div>
          <div
            v-if="!item.isShare"
            class="un-superposition"
          >
            <img
              class="img"
              src="data:image/png;base64,"
            >
            <span class="no-s-text"></span>
          </div>
        </div>
        <div class="main-r">
          <!-- isOptional===1 -->
          <van-checkbox
            :class="getGiveupStatus && item.isSelected ? 'low-light': ''"
            v-model="item.isSelected"
            :disabled="!item.isOptional && !item.isSelected"
          ></van-checkbox>
        </div>
      </div>
      <div
        class="main-box giveup-area"
        @click="handleGiveUp"
      >
        <span class="giveup-text"></span>
        <van-checkbox
          class="giveup-checked"
          v-model="noRights"
        ></van-checkbox>
      </div>
    </div>
    <div class="btn-area">
      <van-button
        round
        type="info"
        @click="finish"
      ></van-button>
    </div>
    <van-loading
      type=""
      class="loading"
      color="#1989fa"
      v-if="loading"
    />
  </div>
</template>
<script>
import { mapMutations, mapActions, mapGetters } from 'vuex'
export default {
  props: {
    orderRightInfos: {
      type: Array,
      default: () => {
        return []
      },
    },
  },
  computed: {
    ...mapGetters('orderRight', ['getOrderRightInfos', 'getOrderProInfo', 'getGiveupStatus']),
    noRights: {
      get(v) {
        return this.getGiveupStatus
      },
      set(v) {
        this.setGiveupStatus(v)
      },
    },
  },
  filters: {
    activedFilter(val) {
      switch (val) {
        case 'S':
          return ''
        case 'F':
          return ''
        case 'AUTO':
          return ''
        default:
          return ''
      }
    },
    giftTypeFilter(val) {
      switch (val) {
        case 'jfmall':
          return ''
        case 'casarte':
          return ''
        case 'entity':
          return ''
        case 'virtual':
          return ''
        default:
          return ''
      }
    },
    limitTypeFilter(val) {
      switch (val) {
        case 'model':
          return ''
        case 'price':
          return ''
        case 'appoint':
          return ''
        default:
          return ''
      }
    },
  },
  data() {
    return {
      loading: false,
      checked: false,
    }
  },
  watch: {
    getOrderRightInfos(val) {
      // 
      if (val) {
        let status = val.every(item => {
          return item.isSelected === 0
        })
        this.setGiveupStatus(status)
      }
    },
  },
  methods: {
    ...mapActions('orderRight', ['selectOrderRights']),
    ...mapMutations('orderRight', ['setSelectedRights', 'setGiveupStatus', 'showDesc']),
    /**
     * @description 
     */
    finish() {
      this.setSelectedRights()
      this.$emit('rightFinished')
    },
    /**
     * @description 
     */
    async handleChecked(item) {
      if (item.isOptional || (!item.isOptional && item.isSelected)) {
        // 
        item.isSelected = !item.isSelected
        this.loading = true
        let params = {
          sourceSn: this.$route.query.orderTempSn,
          rightsNo: item.rightsNo,
          operationType: item.isSelected ? 'checked' : 'uncheck',
        }
        let _success = await this.selectOrderRights(params)
        if (!_success) {
          //  
          item.isSelected = !item.isSelected
        }
        this.loading = false
        this.setSelectedRights()
      }
    },
    /**
     * @description 
     */
    handleGiveUp() {
      this.noRights = !this.noRights
    },
    handleShowDesc(item) {
      this.showDesc(item)
    },
    filterRightsDesc(val) {
      if (val) {
        val = val
          .replace(/\r\n/gim, '<br>')
          .replace(/\r|\n/gim, '<br>')
          .replace(/&nbsp;/g, ' ')
        return `<div>${val}</div>`
      }
    },
  },
}
</script>
