<template>
  <div
    class="order-coupon"
    @click.stop="close"
  >
    <div
      class="coupon-list-wrap"
      @click.stop
    >
      <div class="title">
        <span class="title-text"></span>
        <!-- <img :src="`${$cdnImageUrl}/cart/icon-close.png`" class="close" @click="close"> -->
        <!-- <img class="close" @click="close" src="@/assets/imgs/common/control-close.png" alt="" /> -->
        <img
          class="close"
          @click="close"
          :src="`${$cdnImageUrl}/common/control-close.png`"
          alt=""
        />
      </div>
      <p class="text">
        <span
          :class="{active: activeTab === 'kl'}"
          @click="changeTab('kl')"
        ></span>
        <span
          :class="{active: activeTab === 'ky'}"
          @click="changeTab('ky')"
        ></span>
      </p>
      <template v-if="activeTab === 'kl'">
        <coupon-list-avali
          :voucher-list="list"
          :streetId="streetId"
          @received="received"
        ></coupon-list-avali>
      </template>
      <template v-else>
        <coupon-list :list="list" @fnSelectVoucher="fnSelectVoucher" @received="received"></coupon-list>
      </template>
      <div
        class="yhqNo"
        v-if="list.length == 0 && !loading"
      >
        <img
          alt
          :src="`${$cdnImageUrl}/cart/yhq-no.png`"
        />
        <p>{{`${tipText}~`}}</p>
      </div>
      <van-loading
        type=""
        color="#1989fa"
        class="vanLoding"
        v-if="loading"
      />
      <!-- <div class="no-more-data"></div> -->
      <div class="btn-wrap">
        <div
          class="btn"
          @click="fnConfirm"
        ></div>
      </div>
    </div>
    <!-- <no-data v-if="!list.length"></no-data> -->
  </div>
</template>

<script>
import couponList from './coupon-list-selected.vue' // 
import couponListAvali from './coupon-list-unselect.vue' // 
// import noData from '../../voucher/components/no-data'
import {
  getStoreCoupon,
  chooseTailCoupons,
  getOrderCoupon,
  getTailCoupons,
  choiceStoreCoupon,
  choiceOrderCoupon,
  platCouponList,
  storeCouponList,
  changeStoreCoupon,
  changePlatCoupon
} from '@/http/service/goods'
import './coupon-list-selected.scss'
export default {
  components: {
    couponList,
    couponListAvali,
    // noData,
  },
  props: {
    streetId: String,
    couponListData: Object,
    reqOrderInfo: {
      default() {
        return []
      }
    },
    storeProdList: Array,
    defaultSelect: String, // tab
  },
  data() {
    return {
      activeTab: this.defaultSelect || 'kl',
      type: '', // 1   2 
      productId: '',
      orderSn: '',
      list: [],
      showVoucherTypeFlag: false,
      plantType: '',
      activityId: '',
      itemType: '',
      couponFrom: '', // 
      showYhq: false,
      loading: false,
      sku: '',
    }
  },
  created() {},
  methods: {
    getCouponForAnyScene() {
      this.list = []
      // 
      if (this.activeTab === 'kl') {
        // 
        if (this.type === '1') {
          this.storeCouponList()
        } else {
          this.platCouponList()
        }
      } else {
        // 
        if (this.type === '1') {
          this.getStoreCoupon()
        } else {
          this.getOrderCoupon()
        }
      }
    },
    received(item, index, status = 2) {
      if (this.couponListData && this.couponListData.type === '1') {
        // 
        this.$util.gioTrackWithVariable('MB15627', {
          ad_coupon_id: item.couponTemplateCode + ''
        })
      } else {
        // 
        this.$util.gioTrackWithVariable('MB15629', {
          ad_coupon_id: item.couponTemplateCode + ''
        })
      }
      this.list[index].displayType = status
      // this.$set()
    },
    platCouponList() {
      this.loading = true
      platCouponList(this.reqOrderInfo)
        .then(res => {
          if (res.success) {
            if (this.activeTab === 'kl') {
              this.list = res.data || []
            }
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    storeCouponList() {
      let _item = this.reqOrderInfo.find(v => v.productId === this.couponListData.productId)
      this.loading = true
      storeCouponList(_item)
        .then(res => {
          if (res.success) {
            if (this.activeTab === 'kl') {
              this.list = res.data || []
            }
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    changeTab(tab) {
      this.activeTab = tab
    },
    /**
     * @description 
     */
    getStoreCoupon() {
      this.loading = true
      getStoreCoupon({
        productId: this.productId,
      }).then(res => {
        if (res.success) {
          res.data.forEach((item, index) => {
            item.voucherType = 1
          })
          if (this.activeTab === 'ky') {
            this.list = [...res.data]
          }
          this.showYhq = !(this.list.length > 0);
          this.loading = false;
        } else {
          this.loading = false
          this.showYhq = true
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 
     */
    getOrderCoupon() {
      this.loading = true
      getOrderCoupon().then(res => {
        if (res.success && res.data) {
          res.data.forEach((item, index) => {
            item.voucherType = 2
          })
          if (this.activeTab === 'ky') {
            this.list = [...res.data]
          }
          // this.list = [...res.data]
          this.loading = false;
          this.showYhq = !(this.list.length > 0);
        } else if (res.success) {
          this.loading = false
        } else {
          this.loading = false
          this.showYhq = true
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description +
     */
    getTailCoupons() {
      getTailCoupons({
        orderSn: this.orderSn,
      }).then(res => {
        if (res.success) {
          // for (let i = 0; i < res.data.length; i++) {
          //   if (!res.data[i].canUse) {
          //     res.data.splice(i, 1)
          //     i--
          //   }
          // }
          res.data.forEach((item, index) => {
            item.voucherType = 2
            //  memberCouponId  couponCode
            if (item.memberCouponId) {
              item.couponCode = item.memberCouponId + ''
            }
          })
          this.list = [...res.data]
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 
     */
    fnSelectVoucher(data) {
      // if (this.$route.query.from === 'tailOrder') {
      //   // 
      //   if (this.memberCouponId === data.couponCode) {
      //     // 
      //     this.activityId = ''
      //     this.itemType = ''
      //   } else {
      //     this.activityId = data.activityId
      //     this.itemType = data.type
      //   }
      //   this.memberCouponId = this.memberCouponId === data.couponCode ? '' : data.couponCode
      //   return
      // }
      // this.couponFrom = data.type

      if (this.couponListData && this.couponListData.type === '1') {
        // 
        this.$util.gioTrackWithVariable('MB15627', {
          ad_coupon_id: data.couponTemplateCode + ''
        })
      } else {
        // 
        this.$util.gioTrackWithVariable('MB15629', {
          ad_coupon_id: data.couponTemplateCode + ''
        })
      }
      const params = {
        couponList: this.list,
        changeCoupon: data
      }
      let changeCoupon = null
      if (this.type === '1') {
        // 
        params.price = this.couponListData.prodInfo.price * this.couponListData.prodInfo.num
        changeCoupon = changeStoreCoupon(params)
      } else {
        params.sos = this.storeProdList
        changeCoupon = changePlatCoupon(params)
      }
      changeCoupon.then((res) => {
        if (res.success) {
          for (const coupon of this.list) {
            const couponIndex = res.data.couponCodeList.findIndex(item => item === coupon.couponCode)
            if (couponIndex >= 0) {
              coupon.isForUse = 1
              coupon.sortIndex = couponIndex
            } else {
              coupon.isForUse = 0
            }
            const subCouponIndex = res.data.supCouponCodeList.findIndex(item => item === coupon.couponCode)
            if (subCouponIndex >= 0) {
              coupon.isSuperposed = 1
              coupon.sortIndex = subCouponIndex
            } else {
              coupon.isSuperposed = 0
            }
          }
          this.list = [...this.list]
          res.message && this.$toast(res.message)
        }
      })
    },
    fnConfirm() {
      if (this.activeTab !== 'ky') {
        this.$emit('update:display', false) // tab
        return
      }
      if (this.$route.query.from === 'tailOrder') {
        const couponParams = (() => {
          const typeKey = this.type === '1' ? { couponCode: 'couponCodeProductList', supCouponCode: 'supCouponCodeProductList' } : { couponCode: 'couponCodeOrderList', supCouponCode: 'supCouponCodeOrderList' }
          const params = {
            [typeKey.couponCode]: [],
            [typeKey.supCouponCode]: []
          }
          for (const item of this.list) {
            item.isForUse && params[typeKey.couponCode].push(item)
            item.isSuperposed && params[typeKey.supCouponCode].push(item)
          }
          if (params[typeKey.couponCode].length > 1) {
            params[typeKey.couponCode].sort((a, b) => {
              return a.sortIndex - b.sortIndex
            })
          }
          if (params[typeKey.supCouponCode].length > 1) {
            params[typeKey.supCouponCode].sort((a, b) => {
              return a.sortIndex - b.sortIndex
            })
          }

          params[typeKey.couponCode] = params[typeKey.couponCode].map(item => item.couponCode)
          params[typeKey.supCouponCode] = params[typeKey.supCouponCode].map(item => item.couponCode)
          return params
        })()
        this.chooseTailCouponsByType(couponParams)
      } else {
        const couponParams = (() => {
          const params = {
            couponCodeList: [],
            supCouponCodeList: []
          }
          for (const item of this.list) {
            item.isForUse && params.couponCodeList.push(item)
            item.isSuperposed && params.supCouponCodeList.push(item)
          }
          if (params.couponCodeList.length > 1) {
            params.couponCodeList.sort((a, b) => {
              return a.sortIndex - b.sortIndex
            })
          }
          if (params.supCouponCodeList.length > 1) {
            params.supCouponCodeList.sort((a, b) => {
              return a.sortIndex - b.sortIndex
            })
          }

          params.couponCodeList = params.couponCodeList.map(item => item.couponCode)
          params.supCouponCodeList = params.supCouponCodeList.map(item => item.couponCode)
          return params
        })()
        this.choiceCouponByType(couponParams)
      }
    },
    /**
     * @description 
     */
    chooseTailCouponsByType (couponParams) {
      const params = this.type === '1' ? { ...couponParams, orderSn: this.orderSn } : { ...couponParams, orderSn: this.orderSn, scope: 'ORDER' }
      chooseTailCoupons(params).then((res) => {
        if (res.success) {
          this.fnShowVoucherType()
        }
        res.message && this.$toast(res.message)
      })
    },
    choiceCouponByType (couponParams) {
      const chooseFun = this.type === '1' ? choiceStoreCoupon({ ...couponParams, sku: this.sku }) : choiceOrderCoupon(couponParams)
      chooseFun.then((res) => {
        res.success && this.fnShowVoucherType()
        res.message && this.$toast(res.message)
      })
    },
    fnShowVoucherType() {
      this.showVoucherTypeFlag = false
      this.$emit('chooseCoupon')
    },
    close() {
      this.$emit('update:display', false)
    },
  },
  computed: {
    tipText() {
      return this.activeTab === 'kl' ? '' : ''
    },
  },
  watch: {
    tipText: {
      handler() {
        // tab
        this.getCouponForAnyScene()
      },
    },
    couponListData: {
      handler(val) {
        if (val && Object.keys(val).length) {
          // debugger
          this.type = val.type
          this.productId = val.productId
          this.sku = val.sku
          this.getCouponForAnyScene() // 
        }
      },
      immediate: true,
      deep: true,
    },
    display(val) {
      if (!val) {
        this.list = [] // 
      }
      if (val !== this.showVoucherTypeFlag) {
        this.showVoucherTypeFlag = val
      }
    },
    showVoucherTypeFlag(val) {
      if (val !== this.display) {
        this.$emit('update:display', val)
      }
    },
  },
}
</script>
<style scoped>
@supports (bottom: env(safe-area-inset-bottom)) {
  .coupon-list-wrap {
    padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom);
  }
}
.text {
  text-align: center;
}
.text span:first-of-type {
  margin-right: 1.26rem;
}
.text span {
  display: inline-block;
  height: 0.54rem;
  position: relative;
}
.text span.active {
  color: #2878ff;
  font-family: PingFangSC, PingFangSC-Medium;
  font-weight: 500;
}
.text span.active::after {
  content: '';
  display: block;
  position: absolute;
  width: 0.66rem;
  height: 0.08rem;
  background-color: #2878ff;
  border-radius: 0.04rem;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
.btn-wrap {
  position: absolute;
  bottom: 0;
}
</style>
