<template>
  <div class="shop-list-container">
    <div class="header">
        <span class="title"></span>
        <span class="close" @click="closeModel"></span>
    </div>
    <div class="content">
        <div class="item" v-for="(item, i) in addressList" :key="item.code">
          <div class="check"> <common-checkbox v-if="canCheck" :checked="item.code === currentShopCode" @changeCheck="changeCheck(item.code)"/></div>
            <div class="left">
              <h6 class="item-title">
                {{item.name}}
                <span class="nearest-flag" v-if="!i">
                  <span class="text"></span>
                </span>
              </h6>
              <div class="item-desc">
                <!-- <div class="location"></div> -->
                <div class="location-area"><img class="location" :src="'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/orders/shop-addr.png'"></div>
                <span>{{item.address}}</span></div>
            </div>
             <div class="right" @click="goAddress(item)">
              <span class="navigation-icon"></span>
              <span class="navigation-text"></span>
            </div>

        </div>
    </div>
  </div>
</template>
<script>
import commonCheckbox from '@/components/checkbox/common-checkbox'
export default {
  components: {
    commonCheckbox
  },
  props: {
    currentShopCode: {
      type: String,
      default: ''
    },
    addressList: {
      type: Array,
      default: () => []
    },
    canCheck: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
    }
  },

  mounted() {
  },
  methods: {
    changeCheck(code) {
      if (code === this.currentShopCode) return
      this.$emit('changeCheck', code)
    },
    /**
       * @description 
      */
    closeModel() {
      this.$emit('closeModel')
    },
    fnConfirm() {
      this.$emit('confirm', this.dateSelect)
    },
    goAddress(item) {
      const that = this;
      let data = {
        latitude: item.latitude,
        longitude: item.longitude,
        name: item.name
      }
      if (!data.latitude || !data.longitude) {
        return ''
      }
      const elat = data.latitude
      const elng = data.longitude
      const encode = encodeURI(`${elng},${elat}`)
      const name = data.name// encodeURI(data.name)
      if (this.$util.isSgRn()) {
        const lat = (elat + '').indexOf('.') !== -1 ? elat : elat.toFixed(6);
        const lng = (elng + '').indexOf('.') !== -1 ? elng : elng.toFixed(6);
        window.postMessage(JSON.stringify({
          type: 'goNewWebview',
          data: {
            newUrl: `https://uri.amap.com/marker?position=${encodeURI(`${lng},${lat}`)}&name=${name}`,
          },
        }));
      } else if (this.$util.isIos() || this.$util.isAndroid()) {
        // window.location = this.getAmapURL()
        setTimeout(function() {
          that.$util.openNewWindow(`https://uri.amap.com/marker?position=${encode}&name=${name}`, true);
        }, 500);
      } else {
        this.$util.openNewWindow(this.getAmapURL(data), true)
      }
    },
    getAmapURL(data) {
      if (!data.latitude || !data.longitude) {
        return ''
      }
      const elat = data.latitude
      const elng = data.longitude
      if (!this.$util.isIos() && !this.$util.isAndroid()) {
        const encode = encodeURI(`${elng},${elat}`)
        const name = encodeURI(data.name)
        return this.$util.openNewWindow(`https://uri.amap.com/marker?position=${encode}&name=${name}`, true);
      }
      let proto = this.$util.isIos() ? 'iosamap://path' : this.$util.isAndroid ? 'amapuri://route/plan' : ''
      const AppUrl = proto + '?t=0&dlat=' + elat + '&dlon=' + elng + '&dname=' + encodeURI(data.name)
      return AppUrl
    },

  },
}
</script>
