<template>
  <div class="captcha-dialog-page">
    <div class="close-btn" @click="close"><img class="close" :src="`${$cdnImageUrl}/order/home_common_control-close.png`" alt=""></div>
    <div class="title" >
      <div class="title-name"></div>
    </div>
    <div class="des">{{desc}}</div>
    <div class="input-field flex">
      <input inputmode="search" ref='inputSMS' class="password-input"  @input="change" v-model='inputValue'>
      <div class="get-messge" :style="{color:color}" @click='resend'> {{canResendMSG?'':`${farToResend}s`}}</div>
    </div>
    <div class="divider"></div>
    <div class="phone" v-if="phone">{{isBankPhone ? '' : ''}}{{phone}}</div>
    <div @click="sumitCaptcha" :style='{"background-color":inputValue.length===6?"#2878FF":"#dddddd"}' class="btn-area">
      
    </div>
  </div>
</template>
<script>

export default {
  props: {
    show: {
      type: Boolean,
    },
    preGetTime: {
      type: Number,
    },
    desc: {
      type: String,
      default: () => ''
    },
    isBankPhone: { // 
      type: Boolean,
      default: () => true
    },
    phone: {
      type: String,
      defalut: () => ''
    }
  },
  data() {
    return {
      inputValue: '',
      now: null,
      fdInterval: null,
    }
  },
  computed: {
    color() {
      return this.canResendMSG ? '#2878FF' : '#999'
    },
    farToResend() {
      // if (!this.now || !this.preGetTime) {
      //   return 60;
      // }
      if (Number(this.now - this.preGetTime) < 0) {
        return 60;
      }
      return (this.now - this.preGetTime) >= 60000 ? 0 : (60 - Number((this.now - this.preGetTime) / 1000).toFixed(0));
    },
    canResendMSG() {
      return this.farToResend === 0;
    }
  },
  watch: {
    show: {
      handler: function(newvalue, ov) {
        if (newvalue) {
          this.now = Date.now();
          this.fdInterval = setInterval(() => {
            this.now = Date.now();
          }, 1000);
          // const that = this;
          this.$nextTick(() => {
            this.$refs.inputSMS.focus();
          })
        } else {
          if (this.fdInterval) clearInterval(this.fdInterval)
        }
      },
      immediate: true
    },
  },
  created() {
    this.now = Date.now();
    this.fdInterval = setInterval(() => {
      this.now = Date.now();
    }, 1000);
  },
  mounted() {
  },
  destroyed() {
    if (this.fdInterval) clearInterval(this.fdInterval)
  },
  methods: {
    resend() {
      this.$emit('resend');
    },
    change(e) {
      console.log(e.keyCode);
      this.inputValue = this.inputValue.replace(/[^\d]/g, '');
      this.inputValue = this.inputValue.replace('e', '');
      console.log(this.inputValue);
      if (this.inputValue.length > 6) this.inputValue = this.inputValue.slice(0, 6)
    },
    /**
     * @description 
     */
    sumitCaptcha() {
      console.log('submit');
      if (this.inputValue.length < 6) { return }
      this.$emit('submit', this.inputValue)
      this.inputValue = '';
    },
    /**
     * @description 
     */
    close() {
      this.$emit('close')
    }
  }
}
</script>
