  filters: {
    // 2019-02-03 11:22:002019-02-03
    dateFormater(val) {
      let ret = ''
      if (val.indexOf('-') !== -1) {
        let dateArr = val.split('-')
        ret = `${dateArr[0]}${dateArr[1]}${dateArr[2]}`
      } else {
        ret = val
      }
      return ret.split('')[1];
    },
    /**
     * 
    */
    weekFormater(val) {
      let week = ''
      if (val) {
        let formatDate = val.replace(/||\//g, '-').replace('', '')
        let weekArray = ['', '', '', '', '', '', '']
        week = weekArray[new Date(formatDate).getDay()]
      }
      return week;
    },
    formatPriceInt(v) {
      return v.split('.')[0];
    },
    formatPriceFloat(v) {
      return '.' + v.split('.')[1];
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (from.path === '/orderInvoice' && sessionStorage.getItem('remarkText')) {
