      } else {
        sessionStorage.removeItem('remarkText')
      }
    })
  },
  beforeRouteLeave(to, from, next) {
    if (to.path === '/orderInvoice') {
      sessionStorage.removeItem('remarkText')
    }
    if (to.path === '/orderInvoice' && this.remarkTextArr.length !== 0) {
      sessionStorage.setItem('remarkText', JSON.stringify(this.remarkTextArr))
    }
    if (source) {
