  computed: {
    // 
    actPayMoney() {
      console.log('diamondPurposeMoney ==>', this.diamondPurposeMoney)
      let finalPrice = this.pageInfo.initPam + this.pageInfo.sam - this.ecardListYHPrice - this.diamondPurposeMoney
      console.log(finalPrice < 0 ? 0 : finalPrice.toFixed(2), 'actPayMoney')
      return finalPrice < 0 ? 0 : finalPrice.toFixed(2)
    },
    // E
    enableUseEcard() {
      return this.actPayMoney - this.pageInfo.sam > 0
    },
    // E
    ecardSelectNum() {
      return this.pushComputedPrice.filter(item => item.cardNo).length
    },
    // 
    ecardListYHPrice() {
      let price = 0
      this.pushComputedPrice.forEach(item => {
        if (item.cardNo) {
          price += item.price
        }
      });
      if (price > this.pageInfo.initPam) {
        return this.pageInfo.initPam
      }
      console.log(price, 'ecardListYHPrice')
      return price > 0 ? price.toFixed(2) : price
    },
    invoiceText() {
      const pageInfo = this.pageInfo
      if (pageInfo?.inv) {
        if (pageInfo.inv.invoiceFlag === 0) {
          return ''
        } else if (pageInfo.inv.it === 2) {
          return '' + '-' + (pageInfo.inv.iti || '')
        } else if (pageInfo.inv.it === 3) {
          return '' + '-' + (pageInfo.inv.iti || '')
        } else if (pageInfo.inv.it === 1) {
          return '' + '-' + (pageInfo.inv.bc || '')
        } else if (pageInfo.inv.it === 4) {
          return '' + '-' + (pageInfo.inv.bc || '')
        } else {
          return ''
        }
      } else {
        return ''
      }
    },
    isExclusive() {
      return this.$route.query.isExclusive || this.getExclusiveStatus
    },
    isWDZ() {
      console.log('this.$store.getters.getHasStore', this.$store.getters.getHasStore);
      return this.$store.getters.getHasStore
