      }
    },
  },
  methods: {
    changeShop(code) {
      changeShop({
        shopCode: code,
        productId: this.selectShopObj.productId,
        storeId: this.selectShopObj.storeId
      }).then(res => {
        if (res.success) {
          if (res.data) {
            this.getPageInfo()
            this.selectShopObj.code = code
          }
        } else {
          this.$toast(res.message)
        }
      })
    },
    noop() {
      this.$util.gioTrackWithVariable('MB15628')
    },
    dealRemark(remarkArr, value) {
      value.map((item, index) => {
        if (remarkArr.length > 0 && remarkArr[index].remarkText) {
          item.remarkText = remarkArr[index].remarkText
        } else {
          item.remarkText = ''
        }
      })
    },
    /**
     * 
    */
    toService() {
      this.$store.dispatch('goPageByType', {
        type: 29,
        params: {
          sceneId: 'cj100'
        }
      })
    },
    openShopList(storeId, productId, code, lockShopFlag) {
      if (lockShopFlag) return
      getShopList({
        storeId,
        productId,
      }).then(res => {
        if (res.success) {
          this.selectShopObj = {
            storeId,
            productId,
            code
          }
          this.shopListAddr = res.data
          this.showShopList = true
        } else {
          this.$toast(res.message)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    fieldBlur(e, index, modelValue) {
      // e.target.value = modelValue
      // this.pageInfo.sos[index].remarkText = modelValue
      if (this.remarkTextArr.length === 0) {
        this.remarkTextArr.push({ key: index, remarkText: modelValue })
      } else {
        this.remarkTextArr.map(item => {
          if (item.key !== index) {
            this.remarkTextArr.push({ key: index, remarkText: modelValue })
          } else {
            this.remarkTextArr[index].remarkText = modelValue
          }
        })
      }
      // this.$forceUpdate()
    },
    remarkInput() {
      this.$forceUpdate()
    },
    ...mapMutations('orderRight', ['setOrderRightInfos', 'setOrderProInfo', 'setSelectedRights', 'setGiveupStatus']),
    platCouponAvali(data) {
      platCouponAvali(data).then(res => {
        if (res.success) {
          this.pageInfo.leftCouponAvaili = res.data
        }
      })
    },
    async storeCouponAvali(data) {
      for await (let v of data) {
        let res = await storeCouponAvali(v)
        if (!res.success) continue;
        this.pageInfo.sos.forEach(item => {
          item.ops.forEach(targ => {
            if (v.productId === targ.proId) {
              targ.leftCouponAvaili = res.data
            }
          })
        })
        this.pageInfo = {
          ...this.pageInfo
        } // 
      }
    },
    init() {
      console.log('======init======')
      this.resetEcard()
      if (!this.paying) {
        this.isUseSGBPay = false
        this.isUseJBPay = false
        this.isUseSeaShellPay = false
        this.isUseDiamondPay = false

        if (this.$route.query.productIds) {
          // 
          const _productIds = this.$route.query.productIds
          const _proList = _productIds.split(',').map(item => {
            return {
              proId: item,
              num: 1,
            }
          })
          this.createOrder(_proList, '1') // AC1
        } else if (this.$route.query._proList) {
          let _proList = this.$route.query._proList
          //  
          if (!Array.isArray(_proList)) {
            const str = decodeURIComponent(_proList)
            try {
              _proList = JSON.parse(str)
            } catch (error) {
            }
          }
          //   changeAddressId
          let locationInfo = this.$util.strJSONParse(localStorage.getItem('location'), {})
          if (locationInfo.aId) {
            const data = {
              address: locationInfo.aId,
            }
            personalApi.changeAddressId(data).then(r => {
              this.createOrder(_proList, this.$route.query.ac, true)
            }).catch(e => {
              this.createOrder(_proList, this.$route.query.ac)
            })
          } else {
            this.createOrder(_proList, this.$route.query.ac)
          }
        } else if (this.$route.query.redkey) {
          // sessionStorage.setItem('create-order-key', this.$route.query.redKey)
          this.createOrder([], '1')
        } else {
          this.getPageInfo(true)
        }
      }
    },
    handleScroll(e) {
      this.$util.throttle(() => {
        let _addrTopRem = this.$refs.addrContainer.getBoundingClientRect().top / this.RootEM
        this.$nextTick(() => {
          if (_addrTopRem <= -1.24 && !this.bottomAddrShow) {
            this.bottomAddrShow = true
          } else if (_addrTopRem > -1.24 && this.bottomAddrShow) {
            this.bottomAddrShow = false
          }
        })
      }, 200)()
    },
    getREM() {
      let rem = parseInt(window.getComputedStyle(document.documentElement, null).getPropertyValue('font-size'))
      return rem
    },
    fnOpenPaymentMode() {
      if (this.confirmedOption) this.currentOption = this.confirmedOption
      this.showPaymentOption = true;
    },
    addCloseWebViewParams() {
      let state = { title: '', url: window.location.href };
      let url = (this.$util.isHaierApp() && window.location.href.indexOf('closewebview') !== -1) ? `${process.env.VITE_ENV ? '' : '/sgmobile'}/orderCreate?closewebview=1` : `${process.env.VITE_ENV ? '' : '/sgmobile'}/orderCreate`
      history.replaceState(state, '', url);
    },
    choosePay(item) {
      this.currentOption = item.name
    },
    confirmPaymentMode() {
      this.confirmedOption = this.currentOption
      this.showPaymentOption = !this.showPaymentOption
      this.updatePay()
    },
    async chooseCoupon () {
      this.isUseSGBPay = false
      this.isUseJBPay = false
      this.isUseSeaShellPay = false
      this.isUseDiamondPay = false
      if (this.$util.isHaierApp()) {
      // U+ 
        const authFlag = await this.$store.dispatch('handleUpAuth')
        if (!authFlag) {
          Toast('')
          return
        }
      }
      if (!this.paying) {
        this.getPageInfo()
      }
      this.couponListShow = false
    },

    checkOrderRight (isReload) {
      this.rightLoding = true
      if (source) {
        source.cancel('cancel requet')
        source = null
      }
      source = axios.CancelToken.source();
      checkOrderRight({
        timeout: isReload ? 10000 : 5000
      }, {
        CancelToken: source.token
      }).then(res => {
        source = null
        if (res.success) {
          if (res.data) {
            this.setOrderRightInfos(res.data.orderRightInfos || []) // 
            this.setSelectedRights(res.data.orderRightInfos || [])
            this.setGiveupStatus(false)

            this.newCantNotGetRights = res.data.rightResCode;
            if (this.newCantNotGetRights === '0') {
              this.cantNotGetRights()
            } else if (isReload) {
              this.$toast('')
            }
          }

          this.rightLoadFail = false
        } else {
          if (isReload) this.$toast('')
          this.rightLoadFail = true
        }
        this.rightLoding = false
      }).catch(() => {
        source = null
        if (isReload) this.$toast('')
        this.rightLoadFail = true
        this.rightLoding = false
      })
    },
    /**
     * E
     */
    getEcardInfo() {
      let params = {
        cardType: 1,
        pageNo: 1,
        pageSize: 20,
      }
      this.$http.post(URL.getSmartEcardListForOrder, params).then(({ data }) => {
        let result = data;
        if (result.success) {
          // this.usableEcardCount = result.data.usableCount
          this.pageInfo = {
            ...this.pageInfo,
            usableEcardCount: result.totalCount || 0
          }
        }
      })
    },

    /**
     * @description createOrder  & getPageInfo 
     */
    handdleOrderInfo(res) {
      this.isOrderFailure = false; // 
      console.log('++++++aaaaaaaa+++++++')
      if (res.success) {
        this.validateAddress()
        this.addCloseWebViewParams()
        this.pageInfo = Object.assign({}, res.data)
        // this.pageInfo.initPam = this.pageInfo.pam - this.pageInfo.tca // 
        this.pageInfo.initPam = this.pageInfo.oam - this.pageInfo.sam //
        // Ecard
        // this.isShowUserEcard = this.pageInfo.ot !== 1 && !(this.$util.isSyBird() || this.$util.isSyBirdH5())
        this.isShowUserEcard = this.pageInfo.canUseEcard

        console.log('this.pageInfo:', this.pageInfo)
        // this.finalPrice = res.data.oam
        this.paymentOptions = this.pageInfo.pays
        this.confirmedOption = this.pageInfo.pyw

        this.diamondPurposeMoney = 0
        if (res.data && res.data.bl) {
          this.calculationList = res.data.bl.map(item => {
            if (item.benefitType === 'diamond') {
              this.diamondUseMoney = item.count > this.pageInfo.initPam ? this.pageInfo.initPam : item.count
              return {
                purposeMoney: Math.floor(this.diamondUseMoney),
                canUseMoney: Math.floor(this.diamondUseMoney),
                detail: item,
                canUse: false,
              }
            } else {
              return {
                purposeMoney: '',
                canUseMoney: '',
                detail: item,
                canUse: false,
              }
            }
          })
        }

        // this.calculationSgbData(res.data)
        this.setOrderRightInfos(res.data.orderRightInfos || []) // 
        this.setSelectedRights(res.data.orderRightInfos || [])
        this.setGiveupStatus(false)

        // 
        this.validateFreight = this.pageInfo.sos.filter(i => {
          return !!i.ssf
        })
        const _orderInfo = []
        if (!this.pageInfo.sos) return
        let _reduceAfOrder = 0
        this.pageInfo.sos.forEach(item => {
          item.ops && item.ops.forEach(targ => {
            _orderInfo.push({
              storeId: targ.osId, // id
              productId: targ.proId, // id
              sku: targ.sku,
              price: targ.price, // 
              num: targ.num, // 
              sceneCode: this.pageInfo.sceneCode
            })
            // 
            if (targ.pma && !isNaN(targ.pma)) {
              _reduceAfOrder += Number(targ.pma)
            }
          })
        })
        this.reduceAfOrder = _reduceAfOrder // 
        this.dealRemark(this.remarkTextArr, this.pageInfo.sos)
        this.reqOrderInfo = _orderInfo
        this.platCouponAvali(_orderInfo)
        this.storeCouponAvali(_orderInfo)
        //  /OCO 
        if (this.pageInfo.dd && !this.pageInfo.book) {
          this.isWaOcOCalc(this.pageInfo.sos)
        }
      } else {
        this.isOrderFailure = true;
        this.orderFailueMsg = res.message || '';
      }
    },

    /**
     * @description  ()
     */
    createOrder(proList, ac, refreshAddress) {
      console.log(proList, '---proList----')
      // let _proList = proList && proList.map(v => {
      //   return {
      //     ...v,
      //     exclusivePriceId: v.exclusivePriceId ? v.exclusivePriceId : this.isExclusive ? this.$route.query.exclusiveId : '',
      //     visitStore: v.visitStore ? v.visitStore : (this.getShareId || ''),
      //   }
      // })
      // console.log('getShareId=', this.getShareId);
      let urlQuery = this.$route.query;
      delete urlQuery._proList
      createOrder({
        ac: Number(ac), // 01
        // ch: this.$util.isHaierApp() ? 61 : 60, // 55IOS50H560707580PC99, h5:60, 61app
        ch: this.isSyBird ? 62 : this.$util.isUplus() ? 61 : 60, // 55IOS50H560707580PC99, h5:60, 61app, 62app
        proList,
        street: this.location.townshipId,
        isOrderRight: '1', //  '1': ; '0': 
        v: 2,
        coupon: this.useCouponList,
        sourceName: this.isSyBird ? '' : this.$util.isSyBirdMP() ? '' : '',
        sceneSource: (this.isSyBird || this.$util.isSyBirdMP()) ? 'SYN' : '',
        // storePackageId: this.$route.query.storePackageId || '',
        // sceneCode: this.$route.query.sceneCode || '',
        // shareId: this.getShareId || this.$route.query.storeId || '',
        shareId: sessionStorage.getItem('shareUserId') || this.$route.query.storeId || '',
        originSource: this.originSource || this.$store.state.traceSource || '',
        // groupId: this.$route.query.groupId || 0, // id
        ...urlQuery,
      }).then(res => {
        // getpageinfo
        if (res.success) {
          if (res.message) {
            this.$toast(res.message)
          }
          if (refreshAddress) {
            this.getPageInfo(true)
          } else {
            this.checkOrderRight()
            this.handdleOrderInfo(res)
          }
        } else {
          if (res.errorCode === 'IdentifyNoSee') {
          //  toast
            this.IdentifyText = res.message
          } else {
            this.$toast(res.message)
          }
          // this.$toast(res.message)
          this.handdleOrderInfo(res)
        }
        // E
        this.getEcardInfo();
      }).catch(() => {
        this.$router.back()
      })
