          // shMo = B2B2C && (styWAOCO)
          let unAbleCount = i.ops.filter(r => {
            return !(r.shMo === 'B2B2C' && (r.sty === 'WA' || r.sty === 'OCO'))
          }).length
          return unAbleCount > 0
        }).length
        // >0
        if (notWaCount > 0) this.isWaOcO = false
      } else {
        this.isWaOcO = false
      }
