    /**
     * @description  
     */
    // C--C=(data.oam-data.sam)*bl[2].quota  C> data.oam-data.sam C = data.oam-data.sam C>bl[2].count/bl[2].proportion C=bl[2].count/bl[2].proportion     = C * bl[2].proportion
    calculationSgbData: function(data) {
      console.log('===============')
      const _this = this
      let _calculationList = []
      //  
      let _sgbDetail = ''
      let _insuranceDetail = ''
      let _seaShellDetail = ''
      let _diamondDetail = ''
      // 
      let _sgbPurposeMoney = 0
      let _insurancePurposeMoney = 0
      let _seaShellPurposeMoney = 0
      let _diamondPurposeMoney = 0
      // 
      let _sgbUseMoney = 0
      let _insuranceUseMoney = 0
      let _seaShellUseMoney = 0
      let _diamondUseMoney = 0
      if (data.bl && data.bl.length > 0) {
        for (var i = 0; i < data.bl.length; i++) {
          if (data.bl[i].benefitType === 'sgb') {
            console.log('', data.bl[i])
            _sgbDetail = data.bl[i]
            // ----------
            _sgbPurposeMoney = _this.mul((data.oam || 0) - (data.sam || 0), _sgbDetail.quota || 0)
            if (_sgbPurposeMoney > (data.oam || 0) - (data.sam || 0)) {
              _sgbPurposeMoney = (data.oam || 0) - (data.sam || 0)
            }
            if (_sgbPurposeMoney > (_sgbDetail.count || 0) / (_sgbDetail.proportion || 0)) {
              _sgbPurposeMoney = _sgbDetail.count / _sgbDetail.proportion
            }
            // 1 
            if (_sgbPurposeMoney < 0.01) {
              _sgbPurposeMoney = 0
            }
            //  ========
            _sgbUseMoney = _this.mul(_sgbPurposeMoney, _sgbDetail.proportion)
            _calculationList.push({
              purposeMoney: _sgbPurposeMoney,
              canUseMoney: _sgbUseMoney,
              detail: _sgbDetail,
              canUse: false,
            })
          } else if (data.bl[i].benefitType === 'insurance') {
            // 
            _insuranceDetail = data.bl[i]
            _insurancePurposeMoney = _this.mul((data.oam || 0) - (data.sam || 0), _insuranceDetail.quota || 0)
            if (_insurancePurposeMoney > (data.oam || 0) - (data.sam || 0)) {
              _insurancePurposeMoney = (data.oam || 0) - (data.sam || 0)
            }
            if (_insurancePurposeMoney > (_insuranceDetail.count || 0) / (_insuranceDetail.proportion || 0)) {
              _insurancePurposeMoney = _insuranceDetail.count / _insuranceDetail.proportion
            }
            // 1
            _insuranceUseMoney = Math.floor(_this.mul(_insurancePurposeMoney, _insuranceDetail.proportion))
            _insurancePurposeMoney = _insuranceUseMoney / _insuranceDetail.proportion || 0
            _calculationList.push({
              purposeMoney: _insurancePurposeMoney,
              canUseMoney: _insuranceUseMoney,
              detail: _insuranceDetail,
              canUse: false,
            })
          } else if (data.bl[i].benefitType === 'seashell') {
            // 
            _seaShellDetail = data.bl[i]
            _seaShellPurposeMoney = _this.mul((data.oam || 0) - (data.sam || 0), _seaShellDetail.quota || 0)
            if (_seaShellPurposeMoney > (data.oam || 0) - (data.sam || 0)) {
              _seaShellPurposeMoney = (data.oam || 0) - (data.sam || 0)
            }
            if (_seaShellPurposeMoney > (_seaShellDetail.count || 0) / (_seaShellDetail.proportion || 0)) {
              _seaShellPurposeMoney = _seaShellDetail.count / _seaShellDetail.proportion
            }
            // 1
            _seaShellUseMoney = Math.floor(_this.mul(_seaShellPurposeMoney, _seaShellDetail.proportion))
            _seaShellPurposeMoney = _seaShellUseMoney / _seaShellDetail.proportion || 0
            _calculationList.push({
              purposeMoney: _seaShellPurposeMoney,
              canUseMoney: _seaShellUseMoney,
              detail: _seaShellDetail,
              canUse: false,
            })
          } else if (data.bl[i].benefitType === 'diamond') {
            // 
            _diamondDetail = data.bl[i]
            _diamondPurposeMoney = _this.mul((data.oam || 0) - (data.sam || 0), _diamondDetail.quota || 0)
            if (_diamondPurposeMoney > (data.oam || 0) - (data.sam || 0)) {
              _diamondPurposeMoney = (data.oam || 0) - (data.sam || 0)
            }
            if (_diamondPurposeMoney > (_diamondDetail.count || 0) / (_diamondDetail.proportion || 0)) {
              _diamondPurposeMoney = _diamondDetail.count / _diamondDetail.proportion
            }
            // 1
            _diamondUseMoney = Math.floor(_this.mul(_diamondPurposeMoney, _diamondDetail.proportion))
            _diamondPurposeMoney = _diamondUseMoney / _diamondDetail.proportion || 0
            _calculationList.push({
              purposeMoney: _diamondPurposeMoney,
              canUseMoney: _diamondUseMoney,
              detail: _diamondDetail,
              canUse: false,
            })
          }
          this.calculationList = _calculationList
          console.log('calculationList', this.calculationList)
        }
      }
      this.sgbDetail = _sgbDetail
      this.sgbPurposeMoney = _sgbPurposeMoney.toFixed(2)
      this.sgbUseMoney = _sgbUseMoney.toFixed(2)

      this.insuranceDetail = _insuranceDetail
      this.insurancePurposeMoney = _insurancePurposeMoney.toFixed(2)
      this.insuranceUseMoney = Math.floor(_insuranceUseMoney)

      this.seaShellDetail = _seaShellDetail
      this.seashellPurposeMoney = _seaShellPurposeMoney.toFixed(2)
      this.seaShellUseMoney = Math.floor(_seaShellUseMoney)

      this.diamondDetail = _diamondDetail
      this.diamondPurposeMoney = _diamondPurposeMoney.toFixed(2)
      this.diamondUseMoney = Math.floor(_diamondUseMoney)

      this.initCheckStatus = [false, false, false, false]
      console.log('sgbDetail:', _sgbDetail, 'sgbPurposeMoney: ', _sgbPurposeMoney, 'sgbUseMoney: ', _sgbUseMoney)
    },
    mul: function(arg1, arg2) {
      let m = 0
      let s1 = ''
      let s2 = ''
      if (arg1 && arg1 != null) s1 = arg1.toString()

      if (arg2 && arg2 != null) s2 = arg2.toString()

      try {
        m += s1.split('.')[1].length
      } catch (e) {}
      try {
        m += s2.split('.')[1].length
      } catch (e) {}

      return (Number(s1.replace('.', '')) * Number(s2.replace('.', ''))) / Math.pow(10, m)
    },
