    async doSubmitAction() {
      // 
      if (!this.pageInfo.aid) {
        Toast('')
        return
      }
      if (!this.pageInfo.sid) {
        Toast('/')
        return
      }
      // 
      if (!this.isValidateAddress) {
        let _confirmData = {
          type: 'validateAddress',
          title: '',
          message: this.isValitateMsg,
        }
        this.fnShowDialogConfirm(_confirmData)
        return
      }
      // 
      let OOS = []
      this.pageInfo.sos.map(item => {
        let _OOS = item.ops.filter(j => {
          return j.hasS === false && j.book === false
        })
        OOS.push(..._OOS)
      })
      if (OOS.length) {
        Toast(`${OOS[0].proN}`)
        return
      }
      // 
      if (this.getOrderRightInfos.length > 0 && this.getSelectedRights.length === 0 && !this.getGiveupStatus) {
        if (!await this.rightRemind()) {
          this.showChoosePicker = true
          return
        } else {
          this.setGiveupStatus(true)
        }
      }
      let _signData = await this.checkMemberContract() // GPMS
      if (_signData.needSign) {
        console.log('')
        // 
        this.needSign = true
        if (_signData.url && _signData.url.indexOf('http:') > -1) {
          _signData.url = _signData.url.replace('http:', '')
        }
        this.signUrl = _signData.url
        this.contractTlpId = _signData.contractTlpId
        this.ctmMtNo = _signData.ctmMtNo
        this.showSignToast = true
      } else if (!_signData.needSign) {
        // 
        if (this.pageInfo.ot === 1 && !this.isEarnest) {
          let wkMessage = `${this.pageInfo.tailTime} -- ${this.pageInfo.longTerm === '1' ? '' : this.pageInfo.tailEndTime}`
          let _confirmData = {
            type: 'isEarnest',
            title: '',
            message: '' + wkMessage,
          }
          await this.fnShowDialogConfirm(_confirmData)
          if (!this.isEarnest) {

          }
        }
        let _changeData = await this.getPriceChange() // 
        if (_changeData.result) {
          // true
          let _confirmData = {
            type: 'priceChange',
            title: '',
            message: '?',
            confirmButtonText: '',
            cancelButtonText: '',
          }
          await this.fnShowDialogConfirm(_confirmData)
        }
        // 
        if (this.isUseSGBPay || this.isUseJBPay || this.isUseSeaShellPay || this.isUseDiamondPay || this.pushComputedPrice.findIndex(item => item.cardNo) !== -1) {
          console.log('--')
          this.popupShow = true // 
          this.sendSmsCaptch() // 
        } else {
          console.log('--')
          this.asynSubmitOrder() // 
        }
      }
    },
    /**
     * @description GPMS
     */
    checkMemberContract() {
      return new Promise((resolve, reject) => {
        checkMemberContract({}).then(res => {
          if (res.success) {
            resolve(res.data)
          } else {
            Toast(res.message)
          }
        })
      })
    },
    /**
     * @description  ()
     */
    getPriceChange() {
      return new Promise((resolve, reject) => {
        getPriceChange({}).then(res => {
          if (res.success) {
            resolve(res)
          } else {
            Toast(res.message)
          }
        })
      })
    },
    /**
     * @description 
     */
    fnShowSignContent: function() {
      this.$util.openNewWindow(encodeURIComponent(this.signUrl), true)
    },
    /**
     * @description 
     */
    signContract() {
      if (this.isSigning) {
        // 
        return
      }
      this.isSigning = true
      signContract({
        contractTlpId: this.contractTlpId,
        ctmMtNo: this.ctmMtNo,
        needSign: true,
        url: this.signUrl,
      }).then(res => {
        this.isSigning = false
        this.showSignToast = false
        if (res.success) {
          Toast('')
          this.needSign = false
          // 
          this.fnSubmit()
        } else {
          Toast(res.message)
        }
      })
    },
    fnShowDialogConfirm(obj) {
      return new Promise((resolve, reject) => {
        this.$dialog
          .confirm({
            title: obj.title || '',
            message: obj.message || '',
            confirmButtonText: obj.confirmButtonText || '',
            cancelButtonText: obj.cancelButtonText || '',
          })
          .then(() => {
            // 
            if (obj.type === 'isEarnest') {
              this.isEarnest = true
            }
            resolve()
          })
          .catch(() => {
            // 
            if (obj.type === 'isEarnest') {
              this.isEarnest = false
            }
            if (obj.type === 'priceChange') {
              this.$router.go(-1)
              return
            }
            resolve()
          })
      })
    },
    /**
     * @description 
     */
    sendSmsCaptch() {
      if (this.submitCAPTCHAOnce <= 5) {
        if (Date.now() - this.preGetTime <= 60000) {
          return;
        }
      }
      this.submitCAPTCHAOnce = 0;
      sendSmsCaptch({
        // mobileNum: this.$store.getters.getUserInfo.phone,
      }).then(res => {
        if (res.success) {
          this.preGetTime = Date.now();
        } else {
          Toast(res.message)
        }
      })
    },
    onInput(key) {
      this.value = (this.value + key).slice(0, 6)
      if (this.value.length === 6) {
        this.popupShow = false // 
        // 
        this.asynSubmitOrder()
      }
    },
    onDelete() {
      this.value = this.value.slice(0, this.value.length - 1)
    },
    // 
    onSubmitCaptcha(value) {
      this.value = value
      this.asynSubmitOrder();
    },
    onCloseCaptchaDialog() {
      this.popupShow = false;
    },
    /**
     * @description 
     */
    asynSubmitOrder() {
      if (this.isSubmit) {
        return
      }
      this.isSubmit = true
      let benefitList = []
      if (this.isUseSGBPay) {
        let sgbPayData = {
          amt: this.sgbPurposeMoney || 0,
          benefitType: 'sgb',
          count: this.sgbPurposeMoney || 0,
        }
        benefitList.push(sgbPayData)
      }
      if (this.isUseJBPay) {
        let insurancePayData = {
          amt: this.insurancePurposeMoney || 0,
          benefitType: 'insurance',
          count: (this.insurancePurposeMoney * this.insuranceDetail.proportion || 0).toFixed(0),
        }
        benefitList.push(insurancePayData)
      }
      if (this.isUseSeaShellPay) {
        let seaShellData = {
          amt: this.seashellPurposeMoney || 0,
          benefitType: 'seashell',
          count: (this.seashellPurposeMoney * this.seaShellDetail.proportion || 0).toFixed(0),
        }
        benefitList.push(seaShellData)
      }
      if (this.isUseDiamondPay) {
        let diamondData = {
          amt: this.diamondPurposeMoney || 0,
          benefitType: 'diamond',
          count: this.diamondPurposeMoney || 0,
        }
        benefitList.push(diamondData)
      }

      //  ecard
      if (this.pushComputedPrice.findIndex(item => item.cardNo) !== -1) {
        let ecardData = {
          amt: this.ecardListYHPrice,
          benefitType: 'ecard',
          count: this.ecardListYHPrice,
          bids: []
        }
        this.pushComputedPrice.forEach(item => {
          if (item.cardNo) {
            ecardData.bids.push(item.id)
          }
        })
        benefitList.push(ecardData)
      }
      console.log(this.pushComputedPrice, 'to submit')

      const remarkInfoList = [];
      this.pageInfo.sos.map(item => {
        remarkInfoList.push({
          storeId: item.si,
          remarkInfo: item.remarkText,
        });
      });
      if (source) {
        source.cancel('cancel requet')
        source = null
      }
      asynSubmitOrder({
        captcha: this.value, //  
        ch: 1,
        isBooking: 0,
        benefitList,
        remarkInfoList,
        isOrderRight: this.getGiveupStatus || this.getOrderRightInfos.length === 0 ? '0' : '1' //  '1': ; '0': 
        // openId: '',
      }).then(res => {
        // 
        let allCount = 0
        if (this.pageInfo.sos.length > 1) {
          this.pageInfo.sos.map(v => {
            allCount = allCount + v.ops[0].num
          })
          // allCount = this.pageInfo.sos.reduce((one, two) => {
          //   debugger
          //   return one.ops[0].num + two.ops[0].num
          // })
        } else if (this.pageInfo.sos.length === 1) {
          allCount = this.pageInfo.sos[0].ops[0].num
        }
        let coupon = 0
        if (this.pageInfo.sos.length > 1) {
          this.pageInfo.sos.map(v => {
            coupon = coupon + (v.ops[0].pca || 0)
          })

          // coupon = this.pageInfo.sos.reduce((one, two) => {
          //   if (one.ops[0].pca === null) {
          //     one.ops[0].pca = 0
          //   }
          //   if (two.ops[0].pca === null) {
          //     two.ops[0].pca = 0
          //   }
          //   return one.ops[0].pca + two.ops[0].pca
          // })
        } else if (this.pageInfo.sos.length === 1) {
          if (this.pageInfo.sos[0].ops[0].pca === null) {
            coupon = 0
          } else {
            coupon = this.pageInfo.sos[0].ops[0].pca
          }
        }
        let proId = ''
        if (this.pageInfo.sos.length > 1) {
          this.pageInfo.sos.map(v => {
            proId = proId + ',' + v.ops[0].proId
          })
          // proId = this.pageInfo.sos.reduce((one, two) => {
          //   return one.ops[0].proId + ',' + two.ops[0].proId
          // })
        } else if (this.pageInfo.sos.length === 1) {
          proId = this.pageInfo.sos[0].ops[0].proId
        }
        // App
        this.$util.gioTrackWithVariable('SubmitOrder', {
          member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
          orderId: res.data ? res.data.os : 'null',
        })
        // //
        if (this.isUseJBPay) {
          gio('track', 'SubmitOrder', {
            productId: proId + '',
            orderId: res.data ? res.data.os : '',
            memberId: this.pageInfo.mid + '',
            productCount: allCount + '',
            couponDiscount: coupon + '',
            totalPrice: this.pageInfo.oam + '',
            deductionWay: 'gold'
          })
        } else if (this.isUseSeaShellPay) {
          gio('track', 'SubmitOrder', {
            productId: proId + '',
            orderId: res.data ? res.data.os : '',
            memberId: this.pageInfo.mid + '',
            productCount: allCount + '',
            couponDiscount: coupon + '',
            totalPrice: this.pageInfo.oam + '',
            deductionWay: 'integral'
          })
        } else if (this.isUseDiamondPay) {
          gio('track', 'SubmitOrder', {
            productId: proId + '',
            orderId: res.data ? res.data.os : '',
            memberId: this.pageInfo.mid + '',
            productCount: allCount + '',
            couponDiscount: coupon + '',
            totalPrice: this.pageInfo.oam + '',
            deductionWay: 'diamond'
          })
        } else if (!this.isUseSGBPay) {
          gio('track', 'SubmitOrder', {
            productId: proId + '',
            orderId: res.data ? res.data.os : '',
            memberId: this.pageInfo.mid + '',
            productCount: allCount + '',
            couponDiscount: coupon + '',
            totalPrice: this.pageInfo.oam + '',
            deductionWay: 'unused'
          })
        }

        this.value = '' // 
        if (res.success) {
          // --- 
          this.$util.gioTrackWithVariable('mall_orderCreate_click_submit', {
            order_id: res.data.os ? res.data.os + '' : 'null'
          })
          this.submitCAPTCHAOnce = 0;
          this.popupShow = false;
          this.paying = true
          // ps  101   100 
          // oa  
          // oi  id
          // os  
          // pyc 
          if (res.data.pyc === 'online') {
            // 
            // utils.openNewWindow(`/sgmobile/orderCashier/${res.data.os}`)
            // setTimeout(() => {
            //   this.$router.back()
            // }, 200)

            if (this.$util.isHaierApp()) {
              let goreplace = () => {
                if (this.$util.isHaierApp() && window.location.href.indexOf('closewebview') !== -1) {
                  this.$router.replace({
                    path: `/orderCashier/${res.data.os}?closewebview=1`,
                  })
                } else {
                  // this.$http.get('333.json?res=' + res.data.os)
                  this.$router.replace({
                    path: `/orderCashier/${res.data.os}`,
                  })
                  // this.$http.get('444.json')
                }
              }
              //  vdn 
              try {
                // this.$http.get('000.json')
                this.$util.uplusReportPageClickEvent({
                  actionCode: '1001',
                  extentInfo: { 'OrderNumber': res.data.os, 'sg_refer_url': referrer }
                }).then(() => {
                  console.log(history.state, 'history state')
                }).catch(() => {
                }).finally(() => {
                  // this.$http.get('111.json')
                  goreplace()
                })
              } catch (error) {
                // this.$http.get('222.json')
                goreplace()
              }
            } else {
              this.$router.replace({
                path: `/orderCashier/${res.data.os}`,
              })
              // if (!history.state && this.$util.isHaierApp()) {
              //   window.location.replace(`${config.HOST}/sgmobile/orderCashier/${res.data.os}`)
              // } else {
              //   this.$router.replace({
              //     path: `/orderCashier/${res.data.os}`,
              //   })
              // }
            }
          } else {
            // 
            this.paying = false
            if (this.$util.isHaierApp()) {
              //  vdn 
              try {
                this.$util.uplusReportPageClickEvent({
                  actionCode: '1001',
                  extentInfo: { 'OrderNumber': res.data.os, 'sg_refer_url': referrer }
                }).then(() => {
                  this.fnOpenPayStatus('submit')
                }).catch(err => {
                  console.log(err, 'uplus report');
                  this.fnOpenPayStatus('submit')
                })
              } catch (err) {
                console.log(err, 'catch');
                this.fnOpenPayStatus('submit')
              }
            } else {
              this.fnOpenPayStatus('submit')
            }
          }
        } else {
          this.isSubmit = false
          if (res.errorCode === 'smsCodeError') {
            Toast(res.message)
            if (this.submitCAPTCHAOnce >= 5) {
              this.$toast('');
              this.removeSmsCaptcha();
              // 
            } else {
              this.submitCAPTCHAOnce = this.submitCAPTCHAOnce + 1;
            }
          } else if (res.errorCode === '999') {
            if (res.data) { // 
              this.showRightPicker = true
              this.noRepertoryRight = res.data.orderRightStorages || []
            } else {
              Toast(res.message || '')
            }
          } else if (res.errorCode === '1002') {
            this.limitPurchaseProduct = res.data.purchaseProduct
            this.showLimitPurchaseProduct = true
          } else {
            Toast(res.message || '')
          }
        }
      })
    },
    removeSmsCaptcha() {
      removeSmsCaptcha({}).then(res => {
        if (res.success) {

        } else {
          // Toast(res.message)
        }
      }).catch(e => {
        Toast(e.message)
      })
    },
    /**
     * 
     */
    goBackUpOne() {
      this.$store.dispatch('doHistoryBack');
    },
    /**
     * @description 
     * @param type submit: pay:
     */
    fnOpenPayStatus(type) {
      this.$router.replace({
        path: '/orderPay',
        query: {
          type,
        },
      })
    },
    /**
     * @description 
     */
    protocolCancel() {
      this.showSignToast = false
    },
    /**
     * @description 
     */
    goBack() {
      this.showRightPicker = false
      this.showChoosePicker = true
    },
    /**
     *
     * 
     */
    removeArrElm(arr, attr, attrVal) {
      arr.splice(arr.findIndex(item => item[attr] === attrVal), 1)
      // 
      this.pushComputedPrice.forEach(item => {
        if (item.cardNo) {
          item.price = item.balance
        }
      });
    },

    /**
     * @description 
     */
    giveUp() {
      //  
      Promise.all(this.noRepertoryRight.map(v => setOrderRights({
        operationType: 'uncheck',
        rightsNo: v.rightsNo
      }))).then(resArr => {
        const getOrderRightInfos = [...this.getOrderRightInfos]

        let giveUpStatus = true

        resArr.forEach((res, index) => {
          if (res.success) {
            getOrderRightInfos.forEach(right => {
              if (right.rightsNo === this.noRepertoryRight[index].rightsNo) right.isSelected = 0
              if (right.isSelected) giveUpStatus = false
            })
          }
        })

        //  getOrderRightInfos
        this.setOrderRightInfos(getOrderRightInfos)
        //  setSelectedRights
        this.setSelectedRights()
        // 
        if (giveUpStatus) this.setGiveupStatus(true)

        this.asynSubmitOrder()
      })
    },
    /**
     * @description 
     */
    rightFinished() {
      this.showChoosePicker = false
    },
    /**
     * @description 
     */
    chooseRight() {
      this.showChoosePicker = true
    },
    /**
     * @description 
     */
    handleOverlay() {
      // console.log(123)
    },
    /**
     * @description 
     */
    openRightPopup() {
      console.log(1111)
    },
    remToPx(rem) {
      return (document.body.clientWidth / 7.5) * rem;
    },
    /**
     * 
    */
    headerBack() {
      console.log('headerBack')
      if (!this.pageInfo.sos) {
        this.$store.dispatch('doHistoryBack')
      } else {
        this.leaveOrderTip = true
      }
    }
  },
}
</script>
