<template>
  <div class="container">
    <div class="header">
      <span class="title">{{title}}</span>
      <span class="close-icon" @click="close"></span>
    </div>
    <div class="logo-cantainer">
      <span class="logo"></span>
    </div>
    <div class="desc-cantainer">
      <span>{{desc}}</span>
    </div>
    <div class="option">
      <div class="btn give-up" @click="giveUp"></div>
      <div class="btn to-service" @click="toService" v-if="!(sceneId === 'cj101' && isSyBird)"></div>
      <div class="btn continue" @click="continuePay"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['title', 'desc', 'sceneId'],
  data() {
    return {
      isSyBird: false
    }
  },

  created() {
    this.isSyBird = this.$util.isSyBird()
  },
  methods: {
    /**
       * @description 
      */
    close() {
      this.$emit('close')
    },
    /**
       * @description 
      */
    giveUp() {
      this.$emit('giveUp')
    },
    /**
     * 
    */
    toService() {
      this.$store.dispatch('goPageByType', {
        type: 29,
        params: {
          sceneId: this.sceneId
        }
      })
    },
    /**
     * 
    */
    continuePay() {
      this.$emit('continuePay')
    },
  },
}
</script>
