<template>
  <ul class="coupon-list">
    <li class="content-wrapper" v-for="(item, index) in list" :key="index" @click="fnSelectVoucher(item)">
      <div class="value-wrapper">
        <coupon-value-label :couponValue='item.couponValue' :textColor="'#ffffff'" :couponType='item.couponType'></coupon-value-label>
        <!-- <span class="val-icon"></span>
        <span class="val-num" :class="{bigVal:isBigVal(item)}">{{item.amount}}</span> -->
        <!-- <div class="valIsAvailable" v-if="item.isAvailable == null ? false :(item.isAvailable == 0 ? true : false)"></div> -->
      </div>
      <div class="info-wrapper">
        <div class="info">
          <p class="info-text">
            <span v-if="item.scope==='PRODUCT'" class="singleItem"></span>
            <span v-if="item.scope==='ORDER'" class="singleItem"></span>
            {{item.description}}
          </p>
          <p class="info-text">{{item.couponTemplateName}}</p>
          <p class="info-text" v-if="item.startTime">{{item.startTime | formatDateEndOfDay}}  {{item.endTime | formatDateEndOfDay}}</p>
          <p class="info-text" v-else>{{item.validityPeriod}}</p>
        </div>
      </div>
      <div class="select-wrapper">
        <img :src="selectIconSrc(item)" class="select-icon" alt="">
        <!-- <img :src="selectIconSrc(item)" v-if="item.isAvailable == null ? true :(item.isAvailable == 0 ? false : true)" class="select-icon" alt=""> -->
      </div>
      <img class="type-icon" :src="iconSrc(item)" alt="">
      <img class="new-coupon-bg" :src="`${$cdnImageUrl}/shop/order-voucher-bg.png`" />
      <img v-if="item.isSuperposed" class="superpose-tag" :src="`${$cdnImageUrl}/shop/order-coupon-diejia.png`"/>
      <!-- <div class="infoTips" v-if="item.isAvailable == null ? false :(item.isAvailable == 0 ? true : false)">
         
      </div> -->
    </li>
  </ul>
</template>

<script>
import couponValueLabel from '@/components/couponValueLabel'
const selectIcon = {
  default: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/orders/icon-coupon-default.png',
  selected: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/checked-orange.svg'
}

export default {
  components: { couponValueLabel },
  props: ['list', 'superposeIds'],
  data() {
    return {
      receiveFlag: false,
    }
  },
  computed: {
    couponIcon () {
      const _couponIcon = {
        ORDER: `${this.$cdnImageUrl}/shop/kaquan.png`,
        SCENE: `${this.$cdnImageUrl}/mine/mine-voucher-scene.png`,
        // ORDER: `${this.$cdnImageUrl}/shop/icon-coupon-plat.png`,
        SHOP: `${this.$cdnImageUrl}/shop/dianpuquan.png`
      }
      return _couponIcon
    },
  },
  mounted() {},
  methods: {
    isBigVal (item) {
      return item.couponValue > 1000
    },
    fnSelectVoucher(data) {
      this.$emit('fnSelectVoucher', data)
    },
    iconSrc (item) {
      let type = item.couponUseType

      if (item.applyType) type = 'SCENE'

      return this.couponIcon[type] || type
    },
    selectIconSrc (item) {
      return selectIcon[item.isForUse ? 'selected' : 'default']
    },
    timeHandler (time = '') {
      return time.split(' ')[0]
    }
  },
}
</script>
